import React, { Component } from 'react';

class PokeEvolutions extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name : this.props.evolution,
            img : ''
        }
    }

    async componentDidMount() {
        const data = await (await fetch(`https://pokeapi.co/api/v2/pokemon/${this.props.evolution}`)).json();
        this.setState({
            img : data.sprites.other['official-artwork'].front_default
        })
    }


    render() {
        const { name, img } = this.state;
        return (
            <div className=" m-0 p-0 container-card">
                <div className="card-evolution border rounded bg-secondary">
                    <img src={img}/>
                    <h6 className="text-light">{name}</h6>
                </div>
                {this.props.children}
            </div>
        )
    }
}

export { PokeEvolutions }