import React, { Component } from 'react';
import { PokeEvolutions } from './evolution';

export class PokeView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            height : 0,
            weight : 0,
            egg_groups : [],
            evolutions : [],
            dAbout : '',
            dEvolution : 'd-none'
        }
        this.handleBack = this.handleBack.bind(this)
        this.handleSelectAbout = this.handleSelectAbout.bind(this);
        this.handleSelectEvolution = this.handleSelectEvolution.bind(this)
    }

    async componentDidMount() {
        var listEvolutions = [];
        const dataSpecie = await (await fetch(`https://pokeapi.co/api/v2/pokemon-species/${this.props.dataview.id}`)).json();
        const evolution_url = dataSpecie.evolution_chain.url;
        const evolutions =  (await (await fetch(evolution_url)).json()).chain;
        
        listEvolutions.push(evolutions.species.name);
        let sub = evolutions.evolves_to;
        while(sub.length > 0) {
            listEvolutions.push(sub[0].species.name)
            sub = sub[0].evolves_to;
        }

        this.setState({
            evolutions : listEvolutions,
            egg_groups : dataSpecie.egg_groups.map(e => e.name)
        })
    }

    handleBack() {
        this.setState({
            display: 'd-none'
        })
    }

    handleSelectAbout() {
        this.setState({
            dAbout : '',
            dEvolution : 'd-none'
        })
    }
 
    handleSelectEvolution() {
        this.setState({
            dAbout : 'd-none',
            dEvolution : ''
        })
    }

    render() {
        const { egg_groups, evolutions, dAbout, dEvolution } = this.state;
        const { img,  name, types, specie, moves, abilities, height, weight, id } = this.props.dataview;

        return (
            <div className={"container-fluid pokeView " }>
                <div className="left-part" onClick={()=>this.props.handleView(null, false)}></div>
                <div className={"container-pokemon  h-100 type-" + types[0] + ' type-' + types[1]}>
                    <div className="top-part h-50">
                        <span class="material-icons" id="btn-back" onClick={()=>this.props.handleView(null, false)}>
                            arrow_back
                        </span>
                        <div className="header d-flex">
                            <div className="poke-info d-flex flex-column justify-content-around w-50 m-0 p-3">
                                <h2 className="text-white" className="text-white">{name}</h2>
                                <div className="types">
                                    {
                                        types.map((t, i) => <span key={i} className="types-item">{t}</span>)
                                    }
                                </div>
                            </div>
                            <div className="text-white w-50 mr-4 p-4 text-right">
                                <h5 className="id-pokemon"> # { id }</h5>
                            </div>
                        </div>
                        <div className="image-container text-center">
                            <img src={img}/>
                        </div>
                    </div>
                    <div className="bottom-part h-50">
                        <nav>
                            <ul>
                                <li className={dAbout=="" ? "selected-"+types[0] + " selected-"+types[1] : ""} onClick={() => this.handleSelectAbout()}>ABOUT</li>
                                <li className={dEvolution=="" ? "selected-"+types[0] + " selected-"+types[1] : ""} onClick={() => this.handleSelectEvolution(this.event)}>EVOLUTIONS</li>
                            </ul>
                        </nav>
                        <div className={"view-about w-75 pt-2 " + dAbout}>
                            <div className="grid p-1">
                                <span >Specie</span>
                                <span className="about-value">{specie}</span>
                            </div>
                            <div className="grid p-1">
                                <span >Abilities</span>
                                <ul className="p-0 m-0">
                                    {
                                        abilities.map((e, i) => <li key={i} className="about-value">{e}</li>)
                                    }
                                </ul>
                            </div>
                            <div className="grid p-1">
                                <span >Egg_Groups</span>
                                <ul className="p-0 m-0">
                                    {
                                        egg_groups.map((e, i) => <li key={i} className="about-value">{e}</li>)
                                    }
                                </ul>
                            </div>
                            <div className="grid p-1">
                                <span >Height</span>
                                <span className="about-value">{height} m</span>
                            </div>
                            <div className="grid p-1">
                                <span>Weight</span>
                                <span className="about-value">{weight} kg</span>
                            </div>
                        </div>
                        <div className={"evolutions-container " + dEvolution}>
                                {
                                    evolutions.map((e,i) =><PokeEvolutions key={i} evolution={e}>
                                        {
                                            i!=0 && 
                                            <span class="material-icons next-evolution">
                                                keyboard_arrow_right
                                            </span> 
                                        }
                                    </PokeEvolutions>)
                                }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}







